<template>
	<div class="login">
		<h1>后台管理系统</h1>
		<el-form
			:model="loginform"
			:rules="loginrules"
			ref="loginform"
			label-width="80px"
		>
			<el-form-item label="用户" prop="username">
				<el-input
					prefix-icon="iconfont icon-biaodankongjianlianxiren"
					v-model="loginform.username"
				></el-input>
			</el-form-item>
			<el-form-item label="密码" prop="userpassword">
				<el-input
					prefix-icon="iconfont icon-mima"
					type="password"
					v-model="loginform.userpassword"
				></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="login">登录</el-button>
				<el-button @click="loginformrest">重置</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
export default {
	name: 'LogiFrom',
	components: {},
	mixins: [],
	props: {},
	data() {
    //自定义验证规则
		/*var validatePass = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入密码'))
			} else {
				if (this.loginform.checkuserpassword !== '') {
					this.$refs.loginform.validateField('checkuserpassword')
				}
				callback()
			}
		}
		var validatePass2 = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请再次输入密码'))
			} else if (value !== this.loginform.userpassword) {
				callback(new Error('两次输入密码不一致!'))
			} else {
				callback()
			}
		}*/
		return {
			//input默认值双向绑定默认值
			loginform: {
				username: 'admin',
				userpassword: '123456',
			},

			//input字符规则
			loginrules: {
				username: [
					{ required: true, message: '请输入用户名', trigger: 'blur' },
					{
						min: 3,
						max: 10,
						message: '用户名长度在 3 到 10 个字符',
						trigger: 'blur',
					},
				],
				userpassword: [
					{ required: true, message: '请输入密码', trigger: 'blur' },
					{
						min: 6,
						max: 15,
						message: '用户名长度在 6 到 15 个字符',
						trigger: 'blur',
					},
				],
			},
		}
	},
	computed: {},
	watch: {},
	created() {
    console.log(window.navigator.userAgent);//判断浏览器在什么电脑端还是手机端
   
	},
	mounted() {
		//给全局绑定onkeydown事件如果按下Enter键就执行登录事件
		document.onkeydown = (e) => {
			e = window.event || e
			if (
				this.$route.path == '/login' &&
				(e.code == 'Enter' || e.code == 'enter')
			) {
				this.login()
			}
		}
	},
	destroyed() {},
	methods: {
		// 用户通过了验证
		/*success(msg) {
      this.isShow = false; // 通过验证后，需要手动隐藏模态框
    },
    // 用户点击遮罩层，应该关闭模态框
    close() {
      this.isShow = false;
    },*/
		loginformrest() {
			//this指向组件实例对象
			this.$refs.loginform.resetFields()
		},
		//预表单验证
		login() {
			//预表单验证
			this.$refs.loginform.validate((valid) => {
				//this.isShow = true
				if (!valid) return
				/*login(this.loginform).then(
					(res) => {
						console.log(res)
						if (res.data.code != 1) {
							this.$message.error(res.data.msg)
						} else {
							this.$message.success('登录成功!')
              //用户密码匹配后台返回token存储在sessionStorage里面
							window.sessionStorage.setItem('token', res.data.msg)
							this.$router.push('/home')
						}
					},
					(err) => {}
				)*/
        this.$store.dispatch('addusername',this.loginform.username)
        this.$message.success('登录成功!')
        this.$router.replace(
           '/Home'
          )
			})
		},
	},
	//组件导航守卫设置单页面背景色
	/*beforeRouteEnter(to, form, next) {
    console.log('====');
		window.document.body.style.backgroundColor = '#efeff4'
		next()
	},

	beforeRouteLeave(to, form, next) {
		window.document.body.style.backgroundColor = ''
		next()
	},*/
  //组件生命周期钩子函数设置单页面背景色
	beforeCreate() {
		document
			.querySelector('body')
			.setAttribute('style', 'background-color:#efeff4;')
	},
	beforeDestroy() {
		document.querySelector('body').removeAttribute('style')
	},
}
</script>
<style lang='less' scoped>
  /*登录*/
.login {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 400px;
	height: 400px;
	padding: 10px;
	background-color: #f8f8f8;
	border: 1px solid #f8f8f8;
	transform: translate(-50%, -50%);
}
.login h1 {
	text-align: center;
	font-weight: 400;
	margin: 30px 0 30px 0;
}
</style>
